<template>
  <a-row type="flex" align="middle">
    <a-button v-if="!detail.id" type="link" @click="goLogin">
      登录
    </a-button>
    <a-dropdown v-else placement="bottomCenter">
      <a class="ant-dropdown-link" style="margin:0 20px;line-height: 32px">
        <a-row type="flex" align="middle">
          <template>
            <a-avatar v-if="detail.avatar" :src="detail.avatar | cdn" />
            <a-avatar v-else icon="user" style="background: #1890ff;" />
          </template>
          <span style="margin-left:8px">
            {{ detail.name }}
          </span>
        </a-row>
      </a>

      <a-menu slot="overlay">
        <a-menu-item>
          <a :href="'/' | accountF" style="padding:8px 40px">
            <a-icon type="appstore" />
            我的系统
          </a>
        </a-menu-item>
        <a-menu-item>
          <a :href="'/user' | accountF" style="padding:8px 40px">
            <a-icon type="user" />
            个人中心
          </a>
        </a-menu-item>
        <a-menu-item>
          <a :href="'/loginList' | accountF" style="padding:8px 40px">
            <a-icon type="login" />
            登录记录
          </a>
        </a-menu-item>
        <a-menu-divider />
        <a-menu-item>
          <a-button
            block
            type="link"
            icon="logout"
            size="small"
            @click="logout"
          >
            退出
          </a-button>
        </a-menu-item>
      </a-menu>
    </a-dropdown>
  </a-row>
</template>

<script>
import { getUserInfo } from "@/api";
import { getAccountTokenId, delAccountToken } from "@/utils";

export default {
  data() {
    return {
      detail: {
        id: getAccountTokenId()
      }
    };
  },
  beforeMount() {
    this.detail.id &&
      getUserInfo().then(data => {
        this.detail = { ...data };
      });
  },
  methods: {
    logout() {
      this.detail = {};
      delAccountToken();
    },
    goLogin() {
      this.$router.goLogin();
    }
  }
};
</script>
