<template>
  <div style="padding: 20px;">
    <a-spin v-if="detailLoading" />
    <a-result
      v-else-if="err.status"
      status="error"
      :title="err.status"
      :subTitle="err.message"
    />
    <a-card v-else style="margin:auto;max-width: 1024px;">
      <span slot="title">
        <TitleIcon :file-type="detail.type" />
        {{ detail.name }}
      </span>
      <a-button slot="extra" type="link" @click="saveAs" :loading="saveLoading">
        另存为
      </a-button>

      <template v-if="previewType">
        <a-progress
          v-if="fileLoading && !saveLoading"
          :percent="percent"
          status="active"
        />
        <a-row v-else-if="data" type="flex" justify="center">
          <img v-if="previewType === 'image'" :src="data" />
          <a-col v-else-if="previewType === 'text'" flex="1">
            <p style="white-space: pre-wrap;">{{ data }}</p>
          </a-col>
        </a-row>
      </template>
      <template v-else>
        <a-row type="flex" justify="center">
          文件不支持预览
        </a-row>
      </template>
    </a-card>
  </div>
</template>

<script>
import _ from "lodash";
import axios from "axios";
import { getFileInfo, getFileAuth } from "@/api";
import { origins } from "@/dict";
import { fileToUrl, urlToImage, getAccountToken } from "@/utils";
import TitleIcon from "@/components/TitleIcon";

export default {
  name: "FileInfo",
  components: { TitleIcon },
  data() {
    return {
      fileLoading: true,
      detailLoading: true,
      detail: { url: "" },
      data: undefined,
      percent: 0,
      err: {},
      saveLoading: false
    };
  },
  computed: {
    previewType() {
      const { type } = this.detail;
      return /^image\//.test(type)
        ? "image"
        : /^text\//.test(type)
        ? "text"
        : "";
    }
  },
  watch: {
    "detail.url"() {
      this.handlePreview();
    }
  },
  mounted() {
    this.getUrl();
  },
  methods: {
    getUrl() {
      const { hash } = this.$route.params;
      const { name } = this.$route.query;

      this.detailLoading = true;
      getFileInfo(hash)
        .then(detail => {
          let format = detail.type.replace(/.+\/(.+?)(;.+)?$/, "$1");
          if (format === "plain") {
            format = "txt";
          }

          this.detail = {
            ...detail,
            format,
            name: name || `${hash}.${format}`
          };

          this.handleUrl();
        })
        .catch(this.handleErr)
        .finally(() => {
          this.detailLoading = false;
        });
    },
    handleUrl() {
      const { hash } = this.$route.params;
      const { cdn, formatHash } = this.$options.filters;

      if (this.detail.public) {
        this.detail.url = cdn(hash);
      } else {
        this.detail.url = `${origins.OSS}/private/${formatHash(hash)}`;
      }
    },
    handlePreview() {
      if (this.previewType === "image") {
        this.handleImage();
      } else if (this.previewType === "text") {
        this.handleText();
      }
    },
    getFileData(responseType) {
      this.fileLoading = true;
      return this.getFileAuth().then(auth => {
        return axios
          .get(this.detail.url, {
            params: { auth },
            responseType,
            onDownloadProgress: ({ total, loaded }) => {
              this.percent = _.floor((loaded / total) * 100);
            }
          })
          .then(res => res.data)
          .catch(err => {
            this.previewType = "";
            return Promise.reject(err);
          })
          .finally(() => {
            setTimeout(() => {
              this.fileLoading = false;
            }, 400);
          });
      });
    },
    handleImage() {
      this.getFileData("blob").then(data => {
        fileToUrl(data).then(url => {
          url = url.replace(
            /^(data:)application\/octet-stream/,
            `$1${this.detail.type}`
          );
          urlToImage(url)
            .then(() => {
              this.data = url;
            })
            .catch(() => {
              this.previewType = "";
            });
        });
      });
    },
    handleText() {
      this.getFileData().then(data => {
        this.data = data;
      });
    },
    getFileAuth() {
      if (this.detail.public) return Promise.resolve();

      if (!getAccountToken()) {
        this.$router.goLogin();
        return Promise.reject();
      }

      return getFileAuth();
    },
    handleErr(err) {
      this.err = {
        status: _.get(err, "response.status") || err.status,
        message: _.get(err, "response.data") || err.message
      };
    },
    saveAs() {
      if (!this.detail.url) {
        this.$message.loading("加载中");
      } else {
        this.saveLoading = true;
        this.getFileData("blob")
          .then(blob => {
            window.saveAs(blob, this.detail.name);
          })
          .catch(() => {
            this.getFileAuth().then(auth => {
              const url = !auth
                ? this.detail.url
                : `${this.detail.url}?auth=${auth}`;
              window.saveAs(url, this.detail.name);
            });
          })
          .finally(() => {
            setTimeout(() => {
              this.saveLoading = false;
            }, 400);
          });
      }
    }
  }
};
</script>
