<template>
  <a-icon :type="type" theme="twoTone" />
</template>

<script>
export default {
  props: {
    fileType: String
  },
  computed: {
    type() {
      if (/^image\//.test(this.fileType)) {
        return "file-image";
      } else if (/^text\//.test(this.fileType)) {
        return "file-text";
      } else if (/^application\/pdf/.test(this.fileType)) {
        return "file-pdf";
      } else if (/^application\/zip/.test(this.fileType)) {
        return "file-zip";
      }
      return "file";
    }
  }
};
</script>
