export const env = {
  IS_DEV: process.env.NODE_ENV === "development"
};
Object.freeze(env);

export const DOMAIN = location.hostname.replace(/.+?([a-z]+\.cn)$/, '$1');

export const hosts = {
  OSS: `oss.${DOMAIN}`,
  OOS: `oos.${DOMAIN}`
};
Object.freeze(hosts);

export const origins = {
  ACCOUNT_API: `https://account-api.${hosts.OOS}`,
  ACCOUNT_FRONTEND: `https://account.${hosts.OOS}`,
  OSS: `https://${hosts.OSS}`,
  CDN: `https://cdn.jsdelivr.net/gh/lin09/files`
};
Object.freeze(origins);
