import _ from "lodash";
import Vue from "vue";
import VueRouter from "vue-router";
import { origins } from "@/dict";
import { setAccountToken } from "@/utils";
import Page404 from "@/views/404.vue";
import Info from "@/views/Info.vue";

Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  base: `/`,
  routes: [
    {
      name: "404",
      path: "/404",
      component: Page404
    },
    {
      name: "info",
      path: "/:hash",
      component: Info
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (to.name !== "404" && !/^[0-9a-z]{32}$/.test(to.params.hash)) {
    router.replace("/404");
  } else if (to.query.auth) {
    setAccountToken(to.query.auth);

    // 删除auth
    router.replace(
      _.pick(
        {
          ...to,
          query: _.omit(to.query, ["auth"])
        },
        ["name", "params", "query"]
      )
    );
  } else {
    next();
  }
});

router.goLogin = () => {
  window.location.href = `${
    origins.ACCOUNT_FRONTEND
  }/login?redirect=${encodeURIComponent(
    window.location.href.replace(/(\?|&)auth=.+$/, "")
  )}`;
};

export default router;
