import _ from "lodash";
import axios from "axios";
import { notification } from "ant-design-vue";
import { getAccountToken } from "./token";

/**
 * 创建 AxiosInstance
 * @param {Object} config - AxiosRequestConfig
 * @returns {Object} 返回 AxiosInstance
 */
export const createRequest = config => {
  const instance = axios.create(config);

  // 拦截请求
  instance.interceptors.request.use(
    config => {
      if (config.useAuth !== false) {
        config.headers.auth = config.headers.auth || getAccountToken();
      }
      return config;
    },
    error => {
      notification.error({
        message: error.message
      });
      return Promise.reject(error);
    }
  );

  // 拦截回应
  instance.interceptors.response.use(
    response => response.data,
    error => {
      if (error.config.showError !== false) {
        notification.error({
          message: _.get(error, "response.status") || error.message,
          description: _.get(error, "response.data")
        });
      }

      return Promise.reject(error);
    }
  );

  return instance;
};
