import { createRequest, getAccountTokenId } from "@/utils";
import { origins } from "@/dict";

export const accountRequest = createRequest({ baseURL: origins.ACCOUNT_API });

export const fileRequest = createRequest({
  baseURL: origins.OSS,
  useAuth: false
});

export const getUserInfo = () =>
  accountRequest
    .post("/query/all", {
      table: {
        name: "user",
        no_default_field: true,
        fields: ["id", "avatar", "name"],
        filters: [[["id", "=", getAccountTokenId()]]]
      }
    })
    .then(res => res.data[0]);

export const getFileAuth = () => accountRequest.get("/file/auth");

export const getFileInfo = h => fileRequest.get("/info/" + h);
