import _ from "lodash";
import moment from "moment";
import Vue from "vue";
import { origins } from "@/dict";

const formatHash = hash => hash.replace(/^(.{2})(.{2})/, "$1/$2/");

_.forEach(
  {
    unixFormat: (v, f = "LLL") => (v > 0 ? moment.unix(v).format(f) : "-"),
    accountF: path => origins.ACCOUNT_FRONTEND + path,
    formatHash,
    oss: hash => `${origins.OSS}/${formatHash(hash)}`,
    cdn: hash => `${origins.CDN}/${formatHash(hash)}`
  },
  (filter, key) => {
    Vue.filter(key, (data, ...agm) => filter(data, ...agm));
  }
);
